
/* Main Content Styling */
.dashboard-main-content {
  flex: 1;
  padding: 2rem;
  width: calc(100vw - 280px); /* Adjust this to the width of the sidebar */
  height: 100vh;
  overflow-x: hidden;
  overflow-y: fixed;
}


/* Centering Content */
.dashboard-content {
  padding: 3rem;
  width: 100%;
  height: calc(100% - 4rem); /* Ensure it fits in the content area */
  box-sizing: border-box;
}

/* Dashboard Layout */
.dashboard-section {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start; /* Align items to the left */
  gap: 1rem; /* Reduced gap for more compact design */
}

/* Cards Styling */
.dashboard-card {
  background-color: transparent;
  border-radius: 12px;
  padding: 2rem;
  width: calc(33% - 1rem); /* 3 cards per row */
  min-width: 250px;
  color: #333333;
  text-align: center;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}


.dashboard-card:hover {
  transform: scale(1.05); /* Slight zoom effect on hover */
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.3); /* Deeper shadow on hover */
}

/* Welcome Section */
.welcome-section {
  width: 1650px;
  height: 230px;
  background-image: url('/public/welcome.png'); /* Relative to public folder */
  background-size: 70%; /* Adjust image size */
  background-position: center; /* Center the image */
  background-repeat: no-repeat; /* Prevent the image from repeating */
  border-radius: 10px;
  margin-bottom: 2rem;
  display: fixed;
  align-items: center;
  justify-content: center;
  color: #ffffff;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.5); /* Subtle shadow */
}

/* Card Titles */
.card-title {
  font-size: 1.8rem;
  font-weight: bold;
  color: #333333; /* Darker text for better readability */
  margin-bottom: 1rem;
}


/* Contact Admin Card Styling */
.contact-admin-card {
  background-color: #e0f7fa; /* Light cyan background for contact card */
  border: 1px solid #b2ebf2; /* Slightly darker border */
}

.contact-admin-card textarea {
  border-radius: 8px;
  border: 1px solid #ccc; /* Gray border for the textarea */
  padding: 10px; /* Padding inside the textarea */
  width: 100%;
  resize: none; /* Prevent resizing */
}

.red-button {
  background-color: #be171d; /* Red background */
  color: white; /* White text */
  padding: 10px 20px; /* Adjust padding */
  border: none; /* Remove border */
  border-radius: 5px; /* Rounded corners */
  cursor: pointer; /* Pointer on hover */
  transition: background-color 0.3s ease; /* Smooth hover effect */
}

.red-button:hover {
  background-color: #cc0000; /* Darker red on hover */
}

.red-button:disabled {
  background-color: #ff9999; /* Lighter red for disabled state */
  cursor: not-allowed; /* Change cursor for disabled state */
}


/* Button Styling */
.contact-admin-card button {
  background-color: #008CBA; /* Blue background */
  color: white; /* White text */
  padding: 10px 15px; /* Button padding */
  border: none; /* No border */
  border-radius: 5px; /* Rounded corners */
  cursor: pointer; /* Pointer cursor on hover */
  transition: background-color 0.3s; /* Smooth transition */
}

.contact-admin-card button:hover {
  background-color: #005f7f; /* Darker blue on hover */
}

/* Responsive Adjustments */
@media (max-width: 768px) {
  .dashboard-card {
    width: calc(45% - 1rem); /* Two cards per row on smaller screens */
  }

  .welcome-section {
    background-size: 80%; /* Adjust the background size */
  }
}

@media (max-width: 480px) {
  .dashboard-card {
    width: 100%; /* Single-column layout on small screens */
  }

  .welcome-section {
    background-size: 100%; /* Full width image on very small screens */
  }
}

@media (max-width: 768px) {
  .dashboard-card:hover {
    transform: scale(1.03); /* Slight zoom for smaller screens */
  }
}
